import axios from "axios";
const basepath="http://ocr.bottle.com.np";

export function translate(formData){
  return axios.post(`${basepath}/upload`, formData)
}

export function backTranslate(text) {
  return axios.post(`${basepath}/backtranslate?text=${text}`)
}

export function getEmotion(text) {
  return axios.post(`${basepath}/getemotion?text=${text}`)
}

export function getGrammar(text) {
  return axios.post(`${basepath}/grammar?text=${text}`)
}


